<template>
	<view class="webview">
		<view>
			<web-view :src="webSrc"></web-view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				webSrc: '',
				url: ''
			};
		},
		onShow() {
			
		},
		onLoad(opt) {
			let {url} = opt;
			if(!url) {
				return ;
			}
			this.webSrc = decodeURIComponent(url);
		},
		computed: {
			
		},
		methods: {
			
		}
	}
</script>

